import React from "react";
import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import Icon from "./Icon";
import Button from "./Button";

const NAV = [
  {
    text: "Projects",
    url: "/projects",
  },
  {
    text: "About",
    url: "/about",
  },
  {
    text: "Aiya",
    url: "/404",
  },
];

//wrapping HeaderInner to pass location,
// since cannot use useLocation in class Component.

class HeaderInner extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      scroll: false,
      expandedMenu: false,
    };

    this.setWrapperRef = this.setWrapperRef.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("mousedown", this.handleClickOutside);
  }

  componentWillUnmount() {
    window.removeEventListener("mousedown", this.handleClickOutside);
  }

  setWrapperRef(node) {
    this.wrapperRef = node;
  }

  handleClickOutside(event) {
    if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
      this.handleToggleDropdown();
    }
  }

  handleScroll = () => {
    this.setState({ scroll: window.scrollY > 0 });
  };

  handleToggleDropdown = () => {
    this.setState({ expandedMenu: !this.state.expandedMenu });
  };

  render() {
    const { scroll, expandedMenu } = this.state;
    const { location, isDarkTheme } = this.props;
    const currentPath = location && location.pathname;

    return (
      <React.Fragment>
        <header
          className={
            "main-header" +
            (scroll || expandedMenu ? " main-header--scroll" : "")
          }
        >
          <div className="main-header__wrapper">
            <Button
              onClick={this.handleToggleDropdown}
              icon
              btnStyle="link"
              btnSize="sm"
              className="main-header__mobile-nav-button"
            >
              <Icon icon="menu" />
            </Button>
            <Link
              className="main-header__logo"
              to="/"
              onClick={() => window.scrollTo(0, 0)}
            >
              Philarty
            </Link>
            <nav className="main-header__nav--desktop">
              {NAV.map((link) => (
                <Link
                  key={link.text}
                  to={link.url}
                  className={currentPath.includes(link.url) ? "active" : ""}
                  onClick={() => window.scrollTo(0, 0)}
                >
                  {link.text}
                </Link>
              ))}
              <Button
                onClick={this.props.onToggleTheme}
                btnSize="md"
                btnStyle="link"
                icon
                ref={this.setWrapperRef}
              >
                <Icon icon={isDarkTheme ? "sun" : "moon"} />
              </Button>
            </nav>
          </div>

          {/* dropdown Menu for mobile */}
          {expandedMenu && (
            <nav ref={this.setWrapperRef} className="main-header__nav--mobile">
              {NAV.map((link) => (
                <Link
                  key={link.text}
                  to={link.url}
                  className={currentPath.includes(link.url) ? "active" : ""}
                  onClick={() => {
                    window.scrollTo(0, 0);
                    this.handleToggleDropdown();
                  }}
                >
                  {link.text}
                </Link>
              ))}
            </nav>
          )}
        </header>

        <div className="header__pseudo" />
      </React.Fragment>
    );
  }
}

//wrapping HeaderInner to pass location,
// since cannot use useLocation in class Component.

function Header(props) {
  const location = useLocation();
  const { ...rest } = props;

  return <HeaderInner location={location} {...rest} />;
}

export default Header;
